export const fetchPeople = () => async (dispatch) => {
  const res = await fetch(`https://swapi.dev/api/people`)
  const data = await res.json()
  
  return dispatch({ type: 'FETCH_PEOPLE', payload: data.results })
};

export const selectPerson = (personName) => {
  return { type: 'SELECT_PEOPLE', payload: personName }
}
    
